export const valRequired = (yup) => {
	return yup.string().required("Vui lng nhp");
};

export const valPhone = (yup) => {
	const phoneRegExp =
		/^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/;
	return yup
		.string()
		.matches(phoneRegExp, "S in thoi khng hp l")
		.required("Khng c  trng");
};
